<template>
    <div class="container mx-auto mt-5 row">

        <div class="col-xl-0 col-lg-0 col-md-0 col-sm-2 col-2"></div>
        <div class="col-xl-0 col-lg-0 col-md-0 col-sm-2 col-2"></div>

        <div class="p-5 text-center mx-auto row col-xl-12 col-lg-12 col-md-12 col-sm-10 col-10"
            style="border-radius:40px; background: rgb(220, 220, 220); display: flex; align-items: center;">

            <div class="col-xl-6 col-lg-6 col-md-12 col-sm-12 col-12">

                <div v-if="displayForm === 'form1'" @submit.prevent="onSubmitForm1">
                    <form>
                        <div class="form-group text-center mx-auto">
                            <div style="display: flex; align-items: center; justify-content: center;">
                                <span class="font-weight-bold" style="margin-right: 5px;">lnikree.com/</span>
                                <input type="text" class="form-control w-50" placeholder="Ex : AhmedKhaled"
                                    v-model="validation.input" pattern="[a-zA-Z0-9]{0,12}" required>
                            </div>
                            <div class="font-weight-bold text-danger m-4">
                                {{ ErrorMsg }}
                            </div>
                        </div>
                        <button type="submit" class="btn btn-primary">Create !</button>
                    </form>
                </div>
                <div v-if="displayForm === 'form2'" @submit.prevent="onSubmitForm2">
                    <form>
                        <div class="form-group text-center mx-auto">
                            <div style="display: flex; align-items: center; justify-content: center;">
                                <div class="" style="display: flex; flex-direction: column; align-items: center;">
                                    <div style="margin-right: 5px;" class="mb-3">What's your name ?</div>
                                    <div>
                                        <input type="text" class="form-control w-100" placeholder="Ex : AhmedKhaled"
                                            v-model="newMember.PersonName" pattern="[a-zA-Z ]{3,20}" required>

                                    </div> <!-- {{ NamePage }} -->
                                </div>
                            </div>
                        </div>
                        <button type="submit" class="btn btn-primary">Next --></button>
                    </form>
                </div>
                <div v-if="displayForm === 'form3'" @submit.prevent="onSubmitForm3">
                    <form>
                        <div class="form-group text-center mx-auto">
                            <div class="" style="display: flex; flex-direction: column; align-items: center;">
                                <div style="margin-right: 5px;" class="mb-3">What's your Job ?</div>
                                <div>
                                    <input type="text" class="form-control w-100" placeholder="Ex : AhmedKhaled"
                                        v-model="newMember.PersonJob" pattern="[a-zA-Z]{2,20}" required>
                                    <!-- {{ NamePage }} -->
                                </div>

                            </div>
                        </div>
                        <button type="submit" class="btn btn-primary">Next --></button>
                    </form>
                </div>
                <div v-if="displayForm === 'form4'" @submit.prevent="onSubmitForm4">
                    <form>
                        <div class="form-group 
                                                                text-center mx-auto">
                            <div style="display: flex; align-items: center; justify-content: center;">
                                <div class="" style="display: flex; flex-direction: column; align-items: center;">
                                    <div style="margin-right: 5px;" class="mb-3">Social Media Accounts :</div>

                                    <div class="">
                                        <input type="text" class="form-control w-100"
                                            placeholder="Ex : Facebbok.com/AhmedKhaled" v-model="newMember.FBLink"
                                            pattern="^https?:\/\/(www\.)?facebook\.com\/[a-zA-Z0-9./-_+]+$" required>
                                    </div>
                                    <div class="">
                                        <input type="text" class="form-control w-100 mt-3"
                                            placeholder="Ex : Twitter.com/AhmedKhaled" v-model="newMember.TwitterLink"
                                            pattern="^https?:\/\/(www\.)?twitter\.com\/[a-zA-Z0-9_\/+-]+$" required>
                                    </div>
                                    <div class="">
                                        <input type="text" class="form-control w-100 mt-3"
                                            placeholder="Ex : Instagram.com/AhmedKhaled" v-model="newMember.InstaLink"
                                            pattern="^https?:\/\/(www\.)?instagram\.com\/[a-zA-Z0-9./-_+]+$$" required>
                                    </div>
                                    <div class="">
                                        <input type="text" class="form-control w-100 mt-3"
                                            placeholder="Ex : https://wa.me/12345" v-model="newMember.WsLink"
                                            pattern="https:\/\/wa\.me\/\d{8,15}" required>
                                    </div>
                                    <div class="">
                                        <input type="text" class="form-control w-100 mt-3"
                                            placeholder="Ex : https://wa.me/12345" v-model="newMember.Portofolio" required>
                                    </div>
                                </div>

                            </div>
                        </div>
                        <button type="submit" class="btn btn-primary">Next --></button>
                    </form>
                </div>
                <div v-if="displayForm === 'form5'" @submit.prevent="onSubmitForm5">
                    <form>
                        <div class="form-group text-center mx-auto">
                            <div style="display: flex; align-items: center; flex-direction: column;">
                                <div style="margin-right: 5px;" class="mb-3">About your self :</div>
                                <div>
                                    <input type="text" class="form-control w-100"
                                        placeholder="Ex :I am programmer, 2 years experince, follow me !"
                                        v-model="newMember.PersonAbout" required>
                                    <!-- {{ NamePage }} -->
                                </div>
                            </div>
                        </div>
                        <button type="submit" class="btn btn-primary">Next --></button>
                    </form>
                </div>
                <div v-if="displayForm === 'form6'" @submit.prevent="onSubmitForm6">
                    <form>
                        <div class="form-group text-center mx-auto">
                            <input type="file" ref="fileInput" @change="pickFile">

                            <div class="test mx-auto text-center mt-3 mb-3"
                                :style="{ 'background-image': `url(${previewImage})` }">
                            </div>
                            <!-- <button class="btn btn-primary" @click="uploadFile">Upload</button> -->

                        </div>
                        <button type="submit" class="btn btn-primary">Next --></button>
                    </form>
                </div>
                <div v-if="displayForm === 'final7'">
                    <div class="mb-3">Your Informaion :</div>
                    <div class="text-left">
                        <div class="">
                            Your Link : <span class="font-weight-bold">linktree.com/{{ newMember.PageName }} </span>.
                        </div>
                        <div class="mt-2">
                            Your Name : <span class="font-weight-bold">{{ newMember.PersonName }} </span>.
                        </div>
                        <div class="mt-2">
                            Your Job : <span class="font-weight-bold">{{ newMember.PersonJob }} </span>.
                        </div>
                        <div class="mt-2" style="word-wrap: break-word;">
                            Social Media Accounts : <br>
                            <div class="font-weight-bold mt-2">
                                - {{ newMember.FBLink }}
                            </div>
                            <div class="font-weight-bold">
                                <!-- {{ WSLink }} -->
                            </div>

                            <div class="font-weight-bold">
                                - {{ newMember.TwitterLink }}
                            </div>
                            <div class="font-weight-bold">
                                <!-- {{ LinkedinLink }} -->
                            </div>
                        </div>
                        <div class="mt-2">
                            Your description : <span class="font-weight-bold"> {{ newMember.PersonAbout }} </span>.

                        </div>

                    </div>
                    <div class="mt-5">
                        <button type="submit" class="btn btn-danger" v-on:click="onSUCCESSSubmitForm6">Ok ?</button>
                    </div>
                    <div class="mt-3">
                        <button type="submit" class="btn btn-success" v-on:click="Again">Again</button>
                    </div>

                </div>
            </div>
            <div class="mx-auto text-center justify-content-center" v-if="displayForm === 'final8'" style="display: flex; justify-content: center; align-items: center; flex-direction: column;">
                <div class="mt-4 mx-auto text-center">
                    Your Link : <br>
                </div>
                <div class="mt-3 mx-auto text-center">
                    <a :href="FinalLink" class="mt-2 text-center font-weight-bold text-decoration-none" target="_blank"> Click Here to go</a>

                </div>
            </div>

            <!-- {{ this.response }} -->
            <div class="col-xl-4 col-lg-4 col-md-12 col-sm-12 col-12 mx-auto">
                <div class="">
                    <img src="@/assets/reg1.gif" alt="" style="width: 100%;">
                </div>
            </div>
        </div>



    </div>
</template>
  
<script>
import axios from 'axios';

export default {
    data() {
        return {
            displayForm: 'form1',
            input: 'Ahmed',
            ErrorMsg: '',
            FinalLink: '',
            previewImage: '',
            response: null,
            validation: {
                input: 'AhmedKhaled'
            },
            newMember: {
                PageName: 'Ahmed2023',
                PersonName: 'Ahmed Khaled',
                PersonJob: 'programmer',
                FBLink: 'https://www.facebook.com/kfddsssk',
                WsLink: 'https://wa.me/12345678',
                InstaLink: 'https://www.instagram.com/kfk.com',
                TwitterLink: 'https://www.twitter.com/kfk',
                Portofolio: 'https://www.linkedin.com/kfk',
                PersonAbout: 'i am 20 years old, i am programmer',
                photoName: ''
            }
        }
    },
    methods: {
        pickFile() {
            let input = this.$refs.fileInput
            let file = input.files
            if (file && file[0]) {
                let reader = new FileReader
                reader.onload = e => {
                    this.previewImage = e.target.result
                }
                reader.readAsDataURL(file[0])
                this.$emit('input', file[0])
            }
            const fileName = this.$refs.fileInput.files[0].name;
            console.log('Selected file:', fileName);
            this.newMember.photoName = fileName;
        },
        onSubmitForm1() {
            let Validation = this.toFormData(this.validation)
            axios.post('http://localhost/linktree-vue/linktree-PageName-Valdation.php', Validation)
                .then(response => {
                    // console.log(response.data);
                    if (response.data === "No") {
                        console.log(response.data);
                        this.displayForm = 'form2'
                    } else if (response.data === "Yes") {
                        this.ErrorMsg = "Sorry, this Name is exists .. Try to choose another Username !"
                    }
                })
                .catch(error => {
                    console.log(error);
                });
        },
        onSubmitForm2() {
            this.displayForm = 'form3'
        },
        onSubmitForm3() {
            this.displayForm = 'form4'
        },
        onSubmitForm4() {
            this.displayForm = 'form5'
        },
        onSubmitForm5() {
            this.displayForm = 'form6'
        },
        onSubmitForm6() {
            this.displayForm = 'final7'
            //Send photo : 
            let formData = new FormData();
            formData.append('file', this.$refs.fileInput.files[0]);
            // console.log(this.$refs.fileInput.files[0].name);

            axios.request({
                method: 'POST',
                url: 'http://localhost/linktree-vue/uploadImage.php',
                data: formData,
                headers: {
                    'Content-Type': 'multipart/form-data'
                }
            }).then(response => {
                console.log(response);
                const fileName = this.$refs.fileInput.files[0].name;

                console.log(fileName);
            }).catch(error => {
                console.error(error);
            });
        },
        //Edit INfo :
        Again() {
            this.displayForm = 'form1'

        },
        onSUCCESSSubmitForm6() {
            let membrForm = this.toFormData(this.newMember)
            axios.post('http://localhost/linktree-vue/linktree-Create-Page.php', membrForm)
                .then(response => {
                    // console.log(response);
                    if (response.data === "done") {
                        this.FinalLink = "http://localhost/linktree-vue/template.php?key=" + '"' + this.newMember.PageName + '"';
                        this.displayForm = 'final8';
                    }
                })
                .catch(error => {
                    console.log(error);
                });
        },
        toFormData: function (obj) {
            var form_data = new FormData();
            for (var key in obj) {
                form_data.append(key, obj[key]);
            }
            return form_data;
        },
    },
}
</script>